body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  background-color: hsl(224, 50%, 90%);
}

.App {
  padding: 10px 20px;
  background-color: hsl(224, 50%, 90%);
}

.intro {
  max-width: 70vw;
  line-height: 1.4em;
}
.repository_row {
  display: grid;
  grid-template-columns: minmax(30vw, 35%) minmax(50vw, 50%)  auto;  
  background: #fff;
  padding: 5px 5px;
}

.repository_name {
    font-weight: bold;
    min-width: 30vw;
}

.repository_description {
    color: hsla(10, 50%, 10%, 0.8);
}

.repository_link > a {
    text-decoration: none;
    color: hsla(10, 50%, 10%, 0.8);
}

.repository_link > a:visited {
    text-decoration: none;
    color: hsla(10, 50%, 10%, 0.8);
}

.repository_link > a:hover {
    color: #000;
    font-weight: bold;
}

.card {
  background: white;
  margin: 30px 10px;
  border-radius: 4px;
  padding: 10px 5px;
  overflow: hidden;
  box-shadow: 0px 2px 3px 1px rgba(0, 10, 40, 0.5), 0px 2px 10px 5px rgba(0, 10, 50, 0.25);
}

